export default {
	data() {
		return {
			// 弹窗显示控制
			isShow: false,
			hidContent: false,
			hidNothing: false
		};
	},
	props: {
		contact: { // 联系人
			type: String, // 类型（必填），目前接受的类型包括：String, Number, Boolean, Object, Array, null（表示任意类型）
			value: ''
		},
		tel: { // 联系电话
			type: String,
			value: ''
		},
		address: { // 详细地址
			type: String,
			value: ''
		}
	},
	methods: {
		/*
		 * 公有方法
		 */
		//隐藏弹框
		hideDialog() {
			this.$data.isShow = false;
		},
		//展示弹框
		showDialog() {
			this.$data.isShow = true;
		},
		showContent() {
			this.$data.hidContent = true;
			this.$data.hidNothing = false;
		},
		hideContent() {
			this.$data.hidContent = false;
			this.$data.hidNothing = true;
		},
		/*
		 * 内部私有方法建议以下划线开头
		 * triggerEvent 用于触发事件
		 */
		_cancelEvent() {
			//触发取消回调
			this.$emit("cancelEvent");
		},
		_confirmEvent() {
			//触发成功回调
			this.$emit("confirmEvent");
		}
	}
}
